import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SalesServiceComponent } from './sales-service.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';



const routes: Routes = [
  {
    path: '',
    component: SalesServiceComponent
  },
  {
    path: 'detail-service',
    loadChildren: () => import('./detail-service/detail-service.module').then(m => m.DetailServiceModule)
  }
]

@NgModule({
  declarations: [SalesServiceComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    IonicModule,
  ]
})
export class SalesServiceModule { }
